import React from 'react';
import AbstractList from '../Core/AbstractList'

const ButtonStyle = {
    marginLeft: "10px"
}


class GoodsCategory extends AbstractList {

	constructor( props ) {
		super( props );

		this.getCreatePanel = this.getCreatePanel.bind( this );
	}

	getEntityName() {
		return "goodsCategory";
	}

	getSearchPanel() {
		return [
			{ id: "EQ:title", type: 'text', label: '' }
		]
	}

	getTableHead() {
		return ["", "" , " " , ""];
	}

	getCreatePanel() {
		return [
			{ id: "title", type: 'text', label: '' }
		];
	}


	convertToTableRow( data ) {
		return (
			<tr key={"item_" + data.id}>
				<td>{data.id}</td>
				<td>{data.title}</td>
				<td>
					<i className="fa fa-times" aria-hidden="true" hidden={data.deleted}></i>
					<i className="fa fa-check" aria-hidden="true" hidden={!data.deleted}></i>
				</td>
				<td>
					<a style={ButtonStyle} href="javascript:void(0);"><i className="fa fa-pencil" aria-hidden="true" onClick={this.editEntity.bind(this, data.id)}></i></a>
				</td>
			</tr>
		);
	}

}


export default GoodsCategory;


